* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* border: 1px solid blue; */
}

body {

}

h1 {
  font-size: 30px;
  font-weight: bold;
  /* margin: 40px 0 60px 0; */
}


nav {
  display: flex;
  justify-content: space-around;
  border: 1px solid red;
  max-width: 1200px;
  margin: 20px auto;
}


form {
  display: flex;
  border: 1px solid blue;
}

#search-input {
  width: 70%;
  max-width: 400px;
  padding: 10px 20px;
  border: 1px solid grey;
  border-radius: 50px;
  font-size: 18px;
}

#search-button {
  padding: 10px 20px;
  background-color: black;
  color: #fff;
  border: none;
  font-size: 18px;
  cursor: pointer;
  border-radius: 50px;
  margin: 0px 10px;
}
